





.assignments__cards {
    width: 86%;
    margin: auto;
    margin-left: 77px;
    margin-top: -80px;
    display: grid;
    place-items: center;
    grid-row-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    list-style: none;
}
.assignmentCards {
    position: relative;
    background-color: #fff;
    cursor: pointer;
    width: 220px !important;
    height: 130px;
    overflow: hidden;
    box-shadow: 0px 2px 8px rgb(0 0 0 / 8%);
    color: #033443;
    border: 1px solid #EBECEE;
    border-radius: 16px;
    letter-spacing: normal;
    transition: all 0.2s ease-out;
}


.assignmentCards {
    position: relative;
    background-color: #fff;
    cursor: pointer;
    width: 95%;
    height: 130px;
    overflow: hidden;
    color: #033443;
    border: 1px solid #EBECEE;
    border-radius: 16px;
    letter-spacing: normal;
    transition: all 0.2s ease-out;
}

.assignmentCards:hover {
    transform: scale(1.06);
}

.assignmentCards > img {
    position: absolute;
    object-fit: contain;
    width: 100%;
    top: 0;
    left: 0;
}

.assignmentCards > h4 {
    font-size: .875rem;
    line-height: 1.25rem;
    font-weight: 600;
    margin-top: 100px;
    margin-left: 15px;
}


