
  
  .jobOpenings {
    background-color: #edf3f3;
    min-height: 100vh;
    padding-bottom: 3%;
  }
  
  .jobOpenings__header {
    background-color: #fff;
    height: 80px;
    margin-top: 10px;
  }
  
  .jobOpenings__container {
    position: relative;
    margin: 0 auto;
    margin-top: 5%;
    /* width: 90%; */
    width: 1010px;
    /* max-width: 1000px; */
    min-height: 100%;
    padding: 0.9375rem;
  
    /* border: 1px solid lightgray; */
    border-radius: 0.3125rem;
    border: 1px solid #d6dae0;
    background: #fff;
    -webkit-transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
  
    margin-bottom: 0.9375rem;
  
    overflow: hidden;
  }
  
  .jobOpenings__container > h4 {
    font-size: 0.8125rem;
    line-height: 1.125rem;
    text-transform: uppercase;
    color: #788699;
    font-weight: 500;
    margin: 0;
    margin-bottom: 0.9375rem;
  }
  
  .loadmore {
    border-top: 1px solid #d6dae0;
    height: 56px;
    margin: -15px;
  
    color: #201e27;
    -webkit-transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    height: 100%;
    width: auto;
    border-radius: 0;
    position: relative;
    cursor: pointer;
    color: #201e27;
    border-color: #d6dae0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 0.375rem 0.75rem;
    border-radius: 0.3125rem;
    font-weight: 600;
    font-size: 0.875rem;
    height: 2.5rem;
    -webkit-transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  .jobOpenings__card {
    display: flex;
  
    flex-direction: column;
  
    overflow: hidden;
    border-radius: 0.4525rem;
    border: 1px solid #d6dae0;
    background: #fff;
  
    /* height: 180px; */
    height: auto;
    cursor: pointer;
  
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    -webkit-transition: -webkit-box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: -webkit-box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1),
      -webkit-box-shadow 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
  
    overflow: hidden;
    border-radius: 0.3125rem;
    border: 1px solid #d6dae0;
    background: #fff;
    -webkit-transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: all 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
  }
  
  .jobOpenings__card:hover {
    box-shadow: 0px 3px 3px 1px rgba(0, 0, 0, 0.1);
  }
  
  .card__section {
    display: grid;
    grid-gap: 1rem;
    margin-bottom: 30px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  
  @media (min-width: 600px) {
    .card__section {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (min-width: 900px) {
    .card__section {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  .jobOpenings__info {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  
    padding: 0.9375rem;
    /* border-bottom: 1px solid #d6dae0; */
    flex-grow: 1;
  }
  
