  .jobOpenings__image {
    background: url("https://images.jumpstart.me/frontend/icons/company_logo_default.svg");
  
    background-size: contain;
    background-size: cover;
    border-radius: 50%;
    /* width: 30%; */
    margin-right: 5px;
    height: 3.625rem;
    width: 3.625rem;
    min-height: 3.625rem;
    min-width: 3.625rem;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin-bottom: 10px;
  }
  
  .application {
    color: #788699;
    -webkit-transition: opacity 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    transition: opacity 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
    opacity: 0;
  }
  
