	-webkit-box-ordinal-group: 2;
	-ms-flex-order: 1;
	order: 1
}

.pagination-common .pagination .pagination-next {
	-webkit-box-ordinal-group: 4;
	-ms-flex-order: 3;
	order: 3
}

.pagination-common .pagination>* {
	border: none
}

.pagination-common .pagination .pagination-list {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	text-align: center;
	-webkit-box-ordinal-group: 3;
	-ms-flex-order: 2;
	order: 2;
	color: #1a1133
}

.pagination-common .pagination .icon {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	background-color: #f7f7f9;
	color: #86878e
}

.pagination-common .pagination a>span {
	height: 2.5rem;
	width: 2.5rem;
	border-radius: 50%
}

.pagination-common .pagination a.is-current {
	background-color: #1a73e8;
	border-color: #1a73e8
}

.pagination-common .pagination a:disabled .icon {
