.modal--onboarding .modal__scroll {
	overflow: visible
}

.modal--onboarding .modal__content {
	background: none;
	border-radius: 0;
	-webkit-box-shadow: none;
	box-shadow: none;
	overflow: visible;
	max-width: 67.5rem;
	pointer-events: none
}

.modal--onboarding .modal__content:before,
.modal--onboarding .modal__content:after {
	display: none
}

.modal--ats-invite .wave-modal {
	position: relative
}

.modal--ats-invite .wave-modal__top-content .banner_image {
	height: 100%;
	width: 100%
}

.modal--ats-invite .wave-modal__top-content .logo {
	width: 6.25rem;
	height: 6.25rem;
	margin-top: 3.125rem
}

.modal--ats-invite .util__text-align-center {
	margin: auto;
	max-width: 21.875rem
}

.modal--ats-invite .util__text-align-center .h2 {
	font-size: 1.25rem
}

.modal--ats-invite .util__text-align-center .square-button {
	margin: 1.875rem 0
}

