.profile__modal__form__fullname {
  display: flex;
  align-items: center;
}

.profile__modal__form__fullname > div:first-child {
  margin-right: 3.22rem;
}

.profile__modal__form__fullname > div > label,
.profile__modal__form__mobile > div > label {
  font-weight: 600;
  color: #201e27;
  display: block;
  font-size: 0.75rem;
  line-height: 1.6;
  margin-bottom: 5px;
}

.profile__modal__form__fullname > div > input,
.profile__modal__phoneInput {
  font-size: 0.875rem !important;
  margin: 0;
  margin-bottom: 13px;
  background: transparent;
  color: #030303;
  caret-color: #2dc5a1;
  display: block;
  height: 2.5rem !important;
  line-height: 1.375rem !important;
  outline: none !important;
  padding: 0 0.875rem 0.1em;
  width: 98%;
  border-radius: 0.3125rem !important;
  border: 1px solid #ddd !important;
  transition: all 0.2s ease-in;
}

.profile__modal__form__fullname > div > input:focus {
  border: 1px solid #000 !important;
}

.profile__modal__form__mobile {
  display: flex;
  align-items: center;
}

.profile__modal__form__mobile > div:first-child {
  margin-right: 2rem;
}

.profile__modal__container {
  margin-bottom: 13px;
  height: 2.5rem;
  border-radius: 0.3125rem !important;
  transition: all 0.2s ease-in;
  font-family: 'Inter', sans-serif !important;
}
.react-tel-input {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  position: relative;
  width: 100%;
}
.react-tel-input .special-label {
  display: none;
  white-space: nowrap;
}
.react-tel-input .invalid-number-message,
.react-tel-input .special-label {
  position: absolute;
  z-index: 1;
  font-size: 13px;
  left: 46px;
  top: -8px;
  background: #fff;
  padding: 0 2px;
}
.react-tel-input .form-control {
  position: relative;
  font-size: 14px;
  letter-spacing: 0.01rem;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  padding-left: 48px;
  margin-left: 0;
  background: #fff;
  border: 1px solid #cacaca;
  border-radius: 5px;
  line-height: 25px;
  height: 35px;
  width: 300px;
  outline: none;
}
.react-tel-input .flag-dropdown {
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 0;
  background-color: #f5f5f5;
  border: 1px solid #cacaca;
  border-radius: 3px 0 0 3px;
}
.react-tel-input .selected-flag {
  outline: none;
  position: relative;
  width: 38px;
  height: 100%;
  padding: 0 0 0 8px;
  border-radius: 3px 0 0 3px;
}
.react-tel-input .selected-flag .flag {
  position: absolute;
  top: 50%;
  margin-top: -5px;
}
.react-tel-input .in {
  background-position: -208px -55px;
}
.react-tel-input .flag {
  width: 16px;
  height: 11px;
  background-image: url(data:image/png;base64,);
}

.react-tel-input .selected-flag .arrow {
  position: relative;
  top: 50%;
  margin-top: -2px;
  left: 20px;
  width: 0;
  height: 0;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 4px solid #555;
}

.profile__modal__container .country-list {
  width: 250px !important;
}

/* .profile__modal__phoneInput:focus + .profile__modal__container {
    border: 1px solid #000 !important;
} */

.profile__modal__phoneInput {
  width: 100% !important;
}

.profile__modal__form > form > textarea {
  height: 200px;
  /* width: 94.5%; */
  width: 100%;
  font-size: 0.9rem !important;
  resize: none !important;
  caret-color: #2dc5a1;
  border: 1px solid #ddd;
  outline: none !important;
  border-radius: 5px;
  padding: 0 0.875rem 0.1em;
  padding-top: 10px;
  transition: border 0.2s ease-in;
}

.profile__modal__form > form > textarea:focus {
  border: 1px solid #000;
}

@media (max-width: 768px) {
  .profile__modal__form__fullname > div > input {
    width: 100% !important;
  }

  .profile__modal__form__fullname > div:first-child {
    margin-right: 4rem;
  }

  .profile__modal__form__fullname > div > label,
  .profile__modal__form__mobile > div > label {
    font-size: 0.95rem;
  }
}

@media (max-width: 540px) {
  .profile__modal__form__fullname > div:first-child {
    margin-right: 1.75rem !important;
  }

  .experience__modal__form > form > label,
  .elsewhere__modal__form > form > label,
  .profile__modal__form > form > label,
  .profile__modal__form__fullname > div > label,
  .profile__modal__form__mobile > div > label {
    font-size: 0.875rem;
  }

  .profile__modal__form__fullname > div > input {
    width: 90% !important;
  }
}

@media (max-width: 414px) {
  .profile__modal__form__fullname > div > input {
    width: 83% !important;
  }

  .profile__modal__form__mobile {
    display: block;
  }

  .profile__modal__form__mobile > div:first-child {
    margin-right: 0px;
  }

  .profile__modal__phoneInput {
    width: 101% !important;
  }

  .profile__modal__form > form > textarea {
    width: 93%;
  }
}
